<script>
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default {
    props: {
        swiperState: {
            type: Object,
        },
    },
    data() {
        return {
            screenWidth: screen.width,
            screenHeight: screen.height,
            ratio: screen.width / screen.height,
        };
    },
    mounted() {
        gsap.registerPlugin(ScrollTrigger);
    },
    watch: {
        swiperState(newValue) {
            console.log(newValue.index);
            if (newValue.index == 2) {
                this.pic4In();
                this.pic5In();
                this.pic6In();
                if (document.querySelector(".new2")) {
                    gsap.to(".new2", { ease: "Power4.out", duration: 0.75, startAt: { rotation: 20, y: 300 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".new2-pad")) {
                    gsap.to(".new2-pad", { ease: "Power4.out", duration: 0.75, startAt: { rotation: 25, y: 100 }, rotation: 0, y: 0 });
                }
                if (document.querySelector(".new2-mb")) {
                    gsap.to(".new2-mb", { ease: "Power4.out", duration: 0.75, startAt: { rotation: 25, y: 75 }, rotation: 0, y: 0 });
                }
                // if (oldvalue.index == 0) {
                //     // 從上至下
                //     console.log("從上至下");
                // }

                // if (oldvalue.index == 2) {
                //     // 從上至下
                //     console.log("Changed");
                // }
            }
        },
    },

    methods: {
        pic4In() {
            gsap.to(".pic4", {
                ease: "power4.out",
                startAt: { objectPosition: "0px 1000px" },

                duration: 1.5,
                objectPosition: "0px 0px",

                overwrite: true,
            });
        },
        pic5In() {
            gsap.to(".pic5", {
                ease: "power4.out",
                duration: 1.5,
                x: 0,
                startAt: { objectPosition: "0px -1000px" },

                objectPosition: "0px 0px",
                overwrite: true,
            });
        },
        pic6In() {
            gsap.to(".pic6", {
                ease: "power4.out",
                duration: 1.5,
                startAt: { objectPosition: "0px 1000px" },
                objectPosition: "0px 0px",

                overwrite: true,
            });
        },
        textIn() {
            gsap.to(".new", {
                ease: "Power4.easeOut",
                delay: 2,

                duration: 3,
                ClipPath: "polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)",
                onComplete: () => {
                    this.textOut();
                },
            });
        },
        textOut() {
            gsap.to(".new", {
                ease: "Power4.easeIn",
                delay: 2,
                duration: 3,

                ClipPath: "polygon(0% 0%, 0% 100%, 100% 100%, 0% 100%)",
                onComplete: () => {
                    this.textIn2();
                },
            });
        },
        textIn2() {
            gsap.to(".new2", {
                ease: "Power4.easeOut",
                delay: 2,

                duration: 3,
                ClipPath: "polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)",
                onComplete: () => {
                    this.textOut2();
                },
            });
        },
        textOut2() {
            gsap.to(".new2", {
                ease: "Power4.easeIn",
                delay: 2,
                duration: 3,
                startAt: { ClipPath: "polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)" },

                ClipPath: "polygon(0% 0%, 0% 100%, 100% 100%, 0% 100%)",
                onComplete: () => {
                    this.textIn();
                },
            });
        },
    },
};
</script>

